.utbf-form {
  position: relative;
}
.utbf-form__row {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 10px;
}
.utbf-form__full-col {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.utbf-form__half-col {
  flex: 1;
  width: 50%;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (max-width: 576px) {
  .utbf-form__half-col {
    width: 100%;
  }
}

#utbf-register-form-success {
  display: none;
}

input.text,
input.title,
input[type=email],
input[type=password],
input[type=date],
input[type=number],
input[type=tel],
input[type=text],
select,
textarea {
  color: #6e6e6e;
  border: 1px solid #ccc;
  padding: 10px 5px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}

.button {
  font-size: 16px;
  padding: 7px 20px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}
.button-primary {
  color: #ffffff;
  background-color: #2EA3F2;
}
.button-large {
  padding: 10px 50px;
}

.error {
  color: red;
  display: none;
}

.wrapper-loader {
  display: none;
  position: fixed;
  background-color: #ffffff;
  opacity: 0.8;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.wrapper-loader .loader {
  position: relative;
  height: 30px;
  width: 30px;
  animation: rotate-spin 0.6s linear infinite;
  -webkit-animation: rotate-spin 0.6s linear infinite;
}
.wrapper-loader .loader svg {
  height: 30px;
  width: 30px;
}
.wrapper-loader .loader svg path {
  fill: #2EA3F2;
}

@-webkit-keyframes rotate-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotate-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
#user__child__other_school {
  display: none;
}

.et_fb_custom_register_form::before {
  content: "" !important;
  background-image: url("../img/register.svg") !important;
  height: 16px;
  background-position: 50%;
  background-size: contain;
  background-repeat: no-repeat;
}